.pagination {
  margin: 2rem 0;
  text-align: center;
}
.pagination .page-numbers {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 0.5rem;
}
.pagination .page-numbers li {
  margin: 0;
  padding: 0;
}
.pagination .page-numbers .page-numbers {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 2.5rem;
  height: 2.5rem;
  padding: 0 0.75rem;
  border-radius: 4px;
  background-color: var(--color-light);
  color: var(--color-text);
  font-weight: 500;
  text-decoration: none;
  transition: all 0.3s ease;
}
.pagination .page-numbers .page-numbers:hover {
  background-color: var(--color-primary);
  color: var(--color-white);
}
.pagination .page-numbers .page-numbers.current {
  background-color: var(--color-primary);
  color: var(--color-white);
}
.pagination .page-numbers .page-numbers.prev, .pagination .page-numbers .page-numbers.next {
  padding: 0 1rem;
  font-weight: 600;
}
.pagination .page-numbers .page-numbers.prev i, .pagination .page-numbers .page-numbers.next i {
  margin: 0 0.25rem;
}
.pagination .page-numbers .page-numbers.dots {
  background: none;
  padding: 0 0.5rem;
}
.pagination .page-numbers .page-numbers.dots:hover {
  background: none;
  color: var(--color-text);
}
@media (max-width: 768px) {
  .pagination .page-numbers {
    gap: 0.25rem;
  }
  .pagination .page-numbers .page-numbers {
    min-width: 2rem;
    height: 2rem;
    padding: 0 0.5rem;
    font-size: 0.875rem;
  }
  .pagination .page-numbers .page-numbers.prev, .pagination .page-numbers .page-numbers.next {
    padding: 0 0.75rem;
  }
}
@media (max-width: 480px) {
  .pagination .page-numbers .page-numbers {
    min-width: 1.75rem;
    height: 1.75rem;
    padding: 0 0.25rem;
    font-size: 0.75rem;
  }
  .pagination .page-numbers .page-numbers.prev, .pagination .page-numbers .page-numbers.next {
    padding: 0 0.5rem;
  }
}